$afterMobileScreen: 601px;
$containerWidth: 1086px;
$mobileFontSize: 33px;
$desktopFontSize: 47.5791px;
$mobileLineHeight: 42px;
$desktopLineHeight: 61px;
$paddingSize: 10px;
$marginSize: 15px;

:export {
  #{"containerWidth"}: $containerWidth;
  #{"mobileFontSize"}: $mobileFontSize;
  #{"desktopFontSize"}: $desktopFontSize;
  #{"desktopLineHeight"}: $desktopLineHeight;
  #{"mobileLineHeight"}: $mobileLineHeight;
  #{"marginSize"}: $marginSize;
}

.container {
  max-width: $containerWidth;
  padding: 0 $marginSize;
  margin: 0 auto;
}

.bigBanners {
  display: grid;
  grid-template-columns: 1fr 1fr;
  @media (min-width: $afterMobileScreen) {
    display: flex;
    flex-wrap: wrap;
    padding: $marginSize 0;
    margin: -$marginSize;
  }
}

.bigBanner {
  margin: $marginSize $marginSize 8px $marginSize;
  transition: all 0.4s;

  &:nth-child(1),
  &:nth-child(2) {
    pointer-events: none;
  }

  &:hover,
  &:focus,
  &:active {
    transform: scale(1.06);
  }

  & > img {
    max-width: 100%;
  }

  @media (min-width: $afterMobileScreen) {
    &:first-child {
      max-width: calc(33.3333% - 30px);
    }

    &:nth-child(2) {
      max-width: calc(66.6666% - 30px);
    }

    &:nth-child(3) {
      max-width: calc(66.6666% - 30px);
    }
    &:last-child {
      max-width: calc(33.3333% - 30px);
    }
  }
}

.originalsWrapper {
  width: 326px;
  margin: $marginSize auto 0 auto;
  padding: 25px $paddingSize $paddingSize $paddingSize;
  background: linear-gradient(
    192.93deg,
    #180026 -149.41%,
    #180026 -130.48%,
    #180026 -111.54%,
    #180026 -92.6%,
    #180027 -73.67%,
    #190027 -54.73%,
    #1a0029 -35.79%,
    #1c012b -16.86%,
    #1f022f 2.08%,
    #240235 21.02%,
    #2a043c 39.95%,
    #320547 58.89%,
    #3d0854 77.83%,
    #4b0a66 96.76%,
    #5d0e7c 115.7%,
    #721397 134.63%,
    #8d18b8 153.57%
  );
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  @media (min-width: $afterMobileScreen) {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    padding: $marginSize 20px;
  }
}
.titleContainer {
  display: flex;
  justify-content: center;
  margin-bottom: $marginSize;
  @media (min-width: $afterMobileScreen) {
    width: calc(100% / 3.5 - 20px);
    flex-direction: column;
    justify-content: flex-start;
    margin: $marginSize $paddingSize;
  }
}
.title {
  color: white;
  font-family: "Gilroy";
  font-style: italic;
  font-weight: 1000;
  font-size: $mobileFontSize;
  line-height: $mobileLineHeight;
}

.gamesLetter {
  margin-left: $paddingSize;
  @media (min-width: $afterMobileScreen) {
    margin: auto 0 0 0;
    line-height: 75%;
  }
}

.originalBanner {
  display: inline-flex;
  width: 82px;
  margin: $paddingSize;
  & > img {
    max-width: 100%;
  }
  transition: all 0.4s;
  &:hover,
  &:focus,
  &:active {
    transform: scale(1.16);
  }
  @media (min-width: $afterMobileScreen) {
    width: calc(100% / 7 - 20px);
    margin: $marginSize $paddingSize;
  }
}
