@import "sass/index";

.footer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-between;
  padding: spacing(8) spacing(10.5);
  align-items: flex-end;
  font-size: spacing(2);

  color: use(--secondary-main, 0.5);

  @media (max-width: $mobileScreen) {
    padding: spacing(8) spacing(4);
  }

  & > * {
    margin: 10px 10px 0;
  }

  @media (max-width: 360px) {
    & > * {
      margin: 10px 0;
    }
  }
}

.breadcrumbs {
  & li:nth-child(2n) {
    margin: spacing(0) spacing(2);
    color: use(--secondary-main, 0.5);

    @media (max-width: $mobileScreen) {
      margin: spacing(0) spacing(1);
    }
  }

  & > ol {
    @media (max-width: $mobileScreen) {
      justify-content: flex-end;
    }
  }
}

.link {
  text-decoration: none;
  color: use(--secondary-main, 0.5);
}

.christmas {
  $christmasColor: #b78fff !important;
  color: $christmasColor;

  & * {
    color: $christmasColor;
  }
}
